<template>
  <div class="ChooseHome" style="z-index: 60;position:fixed;background-size: 100% 100%;width: 100%;height: 100%;" @click="chooseClick($event)">
    <div class="ChooseList">
      <div class="ChooseItem" v-for="(arr,i) in ChooseList" @click="labelClick(arr[Object.keys(arr)[0]])">
        {{Object.keys(arr)[0]}}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Choose",
  data(){
    return{
    }
  },
  methods:{
    chooseClick(e){
      e.stopPropagation()
    },
    labelClick(val){
      this.$emit("ChooseClick",val)
    }
  },
  mounted() {
  },
  beforeDestroy() {

  },
  props:{
    ChooseList:{
      type:Array,
      default:[],
      require:true
    }}
}
</script>

