<template>
  <transition name="fade"  @before-enter="imgBeforeEnter" @enter="imgEnter"  @after-enter="imgAfterEnter" @after-leave="imgAfterLeave" >
    <div style="text-align: center;position:fixed;height: 100%;width: 100%;z-index:80">
      <div  style="width:100%; height:100%;padding-bottom:0;background-size:100% 100%" v-show="videoShow">
        <video :src='videoFile' ref="videoRef" autoplay style="" loop></video>
      </div>
      <img :src="imgFile" ref="img" alt="~" class="imgStyle"/>
      <div class="homeDiv" >
        <div class="homeList" v-for="(menu,i) in ((this.language==='EN'||this.language==='en')?menuListEN:menuListCN)">
          <div class="buttonStyle">
            <el-button @click="menuClick($event,i)" v-text="menu"></el-button>
          </div>
        </div>
      </div>
      <audio ref="audioBGM" loop :src="audioFile"/>

    </div>
  </transition>
</template>

<script>
export default {
  name: "Home.vue",
  data(){
    return{
      menuListCN:["","","","",""],
      menuListEN:["Start","Continue","Load","Setting","Exit"],
      homeHtml:"",
      ImgUrl:"",
      videoShow:false,
      videoUrl:'',
      audioUrl:''
    }
  },
  mounted() {
    this.$bus.$on("homeSet",this.homeSet)
    this.$bus.$on("startPlay",this.startPlay)
    this.$bus.$on('setNewVolume',this.setNewVolume)
    this.$store.state.clickNextSwitch=false
  },
  methods:{
    async menuClick(e,menuIndex){
      if(menuIndex===0){
        //
        this.$bus.$emit("startAnew")
        //
        this.$emit("startInterface")
        this.$store.state.clickNextSwitch=true
      }else if(menuIndex===1){
        e.stopPropagation()
        this.$emit("startInterface",'continue')
        this.$store.state.clickNextSwitch=true
      }else if(menuIndex===2){
        this.$message.error('c')
      }else if(menuIndex===3){
        this.$emit("OpenSetting")
      }else if(menuIndex===4){
        let confirm= await this.$confirm('~', '', {
          distinguishCancelAndClose: true,
          confirmButtonText: '',
          cancelButtonText: ''
        }).catch(e=>e)
       if (confirm==='confirm'){
          window.location.href="about:blank";
          window.close();
       }
      }
    },
    imgBeforeEnter(el){
      el.style.opacity = 0
    },
    imgEnter(el,done){
      // 500ms 1
      Velocity(el, {
        opacity: 1
      }, {
        //  done
        duration: 500,
        complete:done
      })
    },
    imgAfterEnter(){
      //
    },
    imgAfterLeave(el){
      //
    },
    homeSet(img,video,audio){
      this.ImgUrl=img
      this.videoUrl=video
      console.log(video);
      if (this.videoUrl!==""){
        this.videoShow=true
      }
      this.audioUrl=audio
    },
    startPlay(isPlay){
      if (this.videoUrl!==""){
        this.$refs.videoRef.currentTime=0
        if(isPlay)
          this.$refs.videoRef.play()
        else
          this.$refs.videoRef.pause()
      }
      if (this.audioUrl!=="")
      {
        this.$refs.audioBGM.currentTime=0
        if(isPlay){
          this.$refs.audioBGM.load()
          this.$refs.audioBGM.play()
        }else{
          this.$refs.audioBGM.pause()
        }
      }
    },
    setNewVolume(playVolumeValue){
      this.$refs.audioBGM.volume=playVolumeValue/100
      this.$refs.videoRef.volume=playVolumeValue/100
    }
  },
  computed:{
    imgFile(){      //  
      if (this.ImgUrl!=='')
        return './static/BackgroundSource/'+this.ImgUrl;
      else
        return false;
    },
    videoFile(){      //  
      if (this.videoUrl!=='')
        return './static/BackgroundSource/'+this.videoUrl;
      else
        return false;
    },
    audioFile(){      //  
      if (this.audioUrl!=='')
        return './static/BGM/'+this.audioUrl;
      else
        return false;
    },
  },
  beforeDestroy() {
    this.$bus.$off("homeSet")
    this.$bus.$off("startPlay")
    this.$bus.$off('setNewVolume')
  },
  props:{
    language:{
      type:String,
      default:'CN',
      require:true
    },
  },
  components:{}
}
</script>

<style scoped>
.imgStyle{
  z-index:-99;
  height: 100%;
  width: 100%;
  background-size: 100% 100%;
}
video{
  position:absolute;
  left:0;
  top:0;
  width:100%;
  height:100%;
  object-fit: fill;
}
</style>
