<template>
  <transition name="fade"  @before-enter="imgBeforeEnter" @enter="imgEnter"  @leave="imgLeave" @before-leave="imgBeforeLeave">
    <div style="z-index: 55;position:fixed;background-size: 100% 100%;width: 100%;height: 100%" class="SoliloquyBackGround" @click="SoliloquyClick($event)" v-show="SoliloquyShow">
      <div class="SoliloquyText">
        <div :class="textClass">
            <template v-for="(t,index) in SoliloquyContent">
              <span  class="letter" :style="'animation-delay:'+(index*delay)+'ms;'" v-html="t===' '?'&nbsp;':t==='^'?'':t"></span>
              <br v-if="t==='^'"/>
            </template>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import TweenMax from '../statics/TweenMax.min'
let timer
export default {
  name: "Soliloquy",
  data(){
    return{
      animateClassName:'leFadeIn',
      animateLeaveClassName:'',
      textClass:['cssanimation','sequence'],
      SoliloquyContent:"",
      delay:100,
      animateTime:0,
      canClickNext:true,
      SoliloquyShow:false,
      contents:[],
      contentIndex:0,
      jsonKey:''
    }
  },
  methods:{
    SoliloquyClick(e){
      console.log(this.canClickNext);
      if (!this.canClickNext){
        e.stopPropagation()
      }else{
        e.stopPropagation()
        this.contentIndex++
        if(this.contentIndex<this.contents.length){
          clearTimeout(timer)
          this.textClass.push(this.animateLeaveClassName)
          setTimeout(() =>{
            this.setContentText()
          },500)
          return false
        }
        if(this.animateLeaveClassName!==""){
          this.textClass.push(this.animateLeaveClassName)
          setTimeout(() =>{
            this.SoliloquyShow=false
          },500)
          return false
        }
        this.SoliloquyShow=false
        if(this.jsonKey.indexOf("_n") !== -1){
          setTimeout(()=>{
            this.$bus.$emit("nextJson")
          },10)
        }
      }
    },
    imgBeforeEnter(el){
      el.style.opacity = 0
    },
    imgEnter(el,done){
      // 500ms 1
      Velocity(el, {
        opacity: 1
      }, {
        //  done
        duration: 500,
        complete:done
      })
    },
    imgLeave(el,done){
      Velocity(el, {
        opacity: 0
      }, {
        //  done
        duration: 300,
        complete:done
      })
    },
    imgBeforeLeave(el){
      el.style.opacity =1
    },
    imgAfterLeave(el){
      //
    },
    soliloquy(jsonKey,jsonValue){
      this.contentIndex=0
      this.SoliloquyContent=''
      let arr=jsonValue.split("|")
      this.contents=arr[0].split("--")
      if (arr.length>=2){
        this.animateClassName=arr[1]
      }
      if (arr.length===3){
        this.animateLeaveClassName=arr[2]
      }
      //this.$emit("SoliloquyShowChange",true)
      this.jsonKey=jsonKey
      this.SoliloquyShow=true
      this.setContentText()
    },
    setContentText(){
      this.canClickNext=false
      this.textClass=this.textClass.filter((i)=>i!==this.animateClassName)
      this.textClass=this.textClass.filter((i)=>i!==this.animateLeaveClassName)
      this.SoliloquyContent=""
      this.$nextTick(()=>{
        this.SoliloquyContent=this.contents[this.contentIndex]
        this.textClass.push(this.animateClassName)
        timer =setTimeout(()=>{
          this.canClickNext=true
        },this.SoliloquyContent.length*this.delay)
      })
    }
  },
  mounted() {
    this.$bus.$on("soliloquy",this.soliloquy)
  },
  beforeDestroy() {
    this.$bus.$off("soliloquy")
  }
}
</script>

