<template>
  <transition name="fade"  @before-enter="imgBeforeEnter" @enter="imgEnter"  @after-enter="imgAfterEnter" @after-leave="imgAfterLeave" >
    <div style="z-index: 55;position:fixed;background-size: 100% 100%;width: 100%;height: 100%" @click="textBoardClick($event)" >
      <transition name="fade"  @before-enter="imgBeforeEnter" @enter="imgEnter"  @after-enter="imgAfterEnter" @leave="imgLeave" @before-leave="imgBeforeLeave">
        <div class="TextBoardBackGround" v-show="isVisible">
          <div class="TextTopControl" @click="textBoardUpDownClick($event)">
            <i class="TextUpDown"></i>
          </div>
          <div class="TextBottomControl">
            <div class="TextBottomDiv">
              <i class="TextBottomIconBase TextBottomIconDoc"></i>
            </div>
            <div class="TextBottomDiv" @click="replay($event)">
              <i class="TextBottomIconBase TextBottomIconReplay"></i>
            </div>
            <div class="TextBottomDiv" @click="autoPlay(false)">
              <i class="TextBottomIconBase TextBottomIconAutoPlay" ref="autoPlayi"></i>
            </div>
            <div class="TextBottomDiv" @click="autoPlay(true)">
              <i class="TextBottomIconBase TextBottomIconAutoPlay2" ref="autoPlay2Xi"></i>
            </div>
            <div class="TextBottomDiv">
              <i class="TextBottomIconBase TextBottomIconSave"></i>
            </div>
            <div class="TextBottomDiv">
              <i class="TextBottomIconBase TextBottomIconLoad"></i>
            </div>
            <div class="TextBottomDiv" @click="openSetting($event)">
              <i class="TextBottomIconBase TextBottomIconSet"></i>
            </div>
            <div class="TextBottomDiv" @click="returnHome($event)">
              <i class="TextBottomIconBase TextBottomIconHome"></i>
            </div>
          </div>
          <div class="TextBoardMiniRoleLeft">
            <transition name="fade"  @before-enter="imgBeforeEnter" @enter="imgEnter"  @after-enter="imgAfterEnter" @after-leave="imgAfterLeave" >
              <img :src="MiniRoleLeftFile" alt="" style="width: 100%;height: 100%" v-show="MiniRoleLeftUrl!==''">
            </transition>
          </div>
          <div class="TextBoardMiniRoleRight">
            <transition name="fade"  @before-enter="imgBeforeEnter" @enter="imgEnter"  @after-enter="imgAfterEnter" @after-leave="imgAfterLeave" >
             <img :src="MiniRoleRightFile" alt="" style="width: 100%;height: 100%" v-show="MiniRoleRightUrl!==''">
            </transition>
          </div>
          <div class="TextBoardMain">
            <div class="TextBoardNameBackGround">
              <span class="TextBoardNameSpan" style="color: dodgerblue;font-size: 200%">{{textName}}</span>
            </div>
            <div class="TextBoardContentBackGround">
              <div :class="[...textClass,animateClassName]" style="color: dodgerblue;" ref="textRef">
                <span v-for="(t,index) in textContent" class="letter"
                      :style="'animation-delay:'+(index*delay)+'ms;'" >
                  {{t===' '?'&nbsp;':t}}</span>
              </div>
            </div>
          </div>
          <div class="TextBoardMiniRoleLeft">
            <img :src="MiniRoleLeftFile" alt="" style="width: 100%;height: 100%">
          </div>

        </div>
      </transition>
      <audio ref="audioRole" :src="audioRoleFile"/>
    </div>
  </transition>
</template>

<script>
import TweenMax from '../statics/TweenMax.min'
import {GetSettingVolume} from "@/SystemFun";
let time;
export default {
  name: "TextBoard",
  data(){
    return{
      textContent:'',
      delay:100,
      animateTime:0,
      animateClassName:'',
      textClass:['cssanimation','sequence'],
      textName:"",
      AudioUrl:'',
      canClickNext:true,
      MiniRoleLeftUrl:"",
      MiniRoleRightUrl:"",
      isVisible:true,
      isAutoPlay:''
    }
  },
  methods:{
    imgBeforeEnter(el){
      el.style.opacity = 0
    },
    imgEnter(el,done){
      // 500ms 1
      Velocity(el, {
        opacity: 1
      }, {
        //  done
        duration: 500,
        complete:done
      })
    },
    imgAfterEnter(){
      //
    },
    imgLeave(el,done){
      Velocity(el, {
        opacity: 0
      }, {
        //  done
        duration: 300,
        complete:done
      })
    },
    imgBeforeLeave(el){
      el.style.opacity =1
    },
    imgAfterLeave(el){
      //
    },
    textBoardUpDownClick(e){
      e.stopPropagation()
      this.isVisible=!this.isVisible
    },
    textBoardClick(e){
      let animateClassName=this.animateClassName
      if ((!this.isVisible)||(!this.canClickNext)){
        e.stopPropagation()
        this.$nextTick(()=>{
          this.$refs.textRef.classList.remove(this.animateClassName)
        })
        this.animateTime=0
        this.isVisible=true
        this.canClickNext=true
        clearTimeout(time)
        return false
      }
    },
    changeText(valKey,val){
      clearTimeout(time)
      //
      let autoNext=false
      if (valKey.indexOf("_n") !== -1){
        //autoplay _n
        valKey=valKey.replace('_n','')
        autoNext=true
      }
      if (valKey==="")
        this.textName=''
      else if (valKey!=="/")
        this.textName=valKey
      let arr=val.split('|')
      this.AudioUrl=''
      if (arr.length===2){
        this.AudioUrl=arr[1]
        this.$nextTick(()=>{
          this.$refs.audioRole.pause()
          this.$refs.audioRole.load()
          this.$refs.audioRole.volume=GetSettingVolume()
          this.$refs.audioRole.play()
        })
      }
      this.$store.state.AudioRoleUrl=this.AudioUrl
      this.animateTime=0
      this.textContent=''

      this.$refs.textRef.classList.remove(this.animateClassName)
      this.canClickNext=false
      this.$nextTick(()=>{
        this.textContent=arr[0] //
        this.animateTime=((this.textContent.length+2) * this.delay)
        this.$refs.textRef.classList.add(this.animateClassName)
        if (autoNext)
          time=setTimeout(()=>{
            this.canClickNext=true
            this.$bus.$emit("nextJson")
          },this.animateTime)
      })
      this.$store.state.RoleName=this.textName
      this.$store.state.RoleText=this.textContent
    },
    changeMiniPic(jsonKey,jsonVal){
      if (jsonKey.indexOf("") !== -1||jsonKey.indexOf("t_pl") !== -1){
        this.MiniRoleLeftUrl=jsonVal
      }else if (jsonKey.indexOf("") !== -1||jsonKey.indexOf("t_pr") !== -1){
        this.MiniRoleRightUrl=jsonVal
      }
      if(jsonKey.indexOf("_n") !== -1){
        setTimeout(()=>{
          this.$bus.$emit("nextJson")
        },10)
      }
    },
    replay(e){
      e.stopPropagation()
      this.animateTime=0
      let textContent=this.textContent
      this.textContent=''
      this.$refs.textRef.classList.remove(this.animateClassName)
      this.$nextTick(()=>{
        if (this.AudioUrl!==''){
          this.$refs.audioRole.pause()
          this.$refs.audioRole.load()
          this.$refs.audioRole.play()
        }
        this.canClickNext=false
        this.textContent=textContent //
        this.animateTime=(this.textContent.length * this.delay)
        this.$refs.textRef.classList.add(this.animateClassName)

        time=setTimeout(()=>{
          this.canClickNext=true
        },this.animateTime)
      })
    },
    autoPlay(is2X){
      //
      if (this.isAutoPlay!==''){
        //
        if(this.isAutoPlay==='1x')
          this.$refs.autoPlayi.classList.remove("TextBottomIconFocus")
        else
          this.$refs.autoPlay2Xi.classList.remove("TextBottomIconFocus")
        this.isAutoPlay=''
        this.$bus.$emit("isAutoPlay")
        return false
      }
      //
      if (is2X){
        this.delay=30
        this.$refs.autoPlay2Xi.classList.add("TextBottomIconFocus")
        this.isAutoPlay='2x'
      }else{
        this.isAutoPlay='1x'
        this.$refs.autoPlayi.classList.add("TextBottomIconFocus")
      }
      this.$bus.$emit("isAutoPlay")
    },
    openSetting(e){
      this.$emit("OpenSetting")
      e.stopPropagation()
    },
    async returnHome(e){
      e.stopPropagation()
      let confirm= await this.$confirm('~', '', {
        distinguishCancelAndClose: true,
        confirmButtonText: '',
        cancelButtonText: '',
        center: true
      }).catch(ex=>ex)
      if (confirm==='confirm'){
        this.$bus.$emit("startInterface")
      }
    },
    initTextBoard(){
      //
      this.textContent=''
      //this.delay=parseInt(localStorage.getItem('playSpeedValue'))
      this.animateTime=0
      //this.animateClassName=localStorage.getItem('fontDisplayMode')?localStorage.getItem('fontDisplayMode'):"leFadeIn"
      this.textClass=['cssanimation','sequence']
      this.textName=""
      this.AudioUrl=''
      this.canClickNext=true
      this.MiniRoleLeftUrl=""
      this.MiniRoleRightUrl=""
      this.isVisible=true
      this.isAutoPlay=''
    },
    useNewSetting(settings){
      this.delay=settings['playSpeedValue']
      this.animateClassName=settings['fontDisplayMode']
      this.$refs.textRef.style.fontSize=settings['fontSizeValue']+'%'
    },
    setNewVolume(playVolumeValue){
      this.$refs.audioRole.volume=playVolumeValue/100
    }
  },
  computed:{
    audioRoleFile(){
      if (this.AudioUrl!=='')
        return './static/RoleSource/'+this.AudioUrl
      else
        return false;
    },
    MiniRoleLeftFile(){      //  
      if (this.MiniRoleLeftUrl!=='')
        return './static/RoleSource/'+this.MiniRoleLeftUrl
      else
        return false;
    },
    MiniRoleRightFile(){      //  
      if (this.MiniRoleRightUrl!=='')
        return './static/RoleSource/'+this.MiniRoleRightUrl
      else
        return false;
    }
  },
  mounted() {
    this.$bus.$on('changeText',this.changeText)
    this.$bus.$on('changeMiniPic',this.changeMiniPic)
    this.$bus.$on('initTextBoard',this.initTextBoard)
    this.$bus.$on('useNewSetting',this.useNewSetting)
    this.$bus.$on('setNewVolume',this.setNewVolume)
    this.delay=localStorage.getItem('playSpeedValue')?parseInt(localStorage.getItem('playSpeedValue')):100
    this.animateClassName=localStorage.getItem('fontDisplayMode')?localStorage.getItem('fontDisplayMode'):"leFadeIn"
    this.$refs.textRef.style.fontSize=localStorage.getItem('fontSizeValue')?parseInt(localStorage.getItem('fontSizeValue'))+'%':'150%'
  },
  beforeDestroy() {
    this.$bus.$off('changeText')
    this.$bus.$off('changeMiniPic')
    this.$bus.$off('initTextBoard')
    this.$bus.$off('useNewSetting')
    this.$bus.$off('setNewVolume')
  }
}
</script>

