<template>
  <div @click="nextJson">
    <div id="test"></div>
    <Background @nextJson="nextJson" @findChooseLabel="findChooseLabel" @loadAnotherFile="loadAnotherFile"/>
  </div>
</template>

<script>
import Background from "./Background";
import {ReadFile} from "@/SystemFun";
export default {
  name: "Main",
  data(){
    return{
      fileDataArr:[],
      anotherFileDataArr:[],
      jsonNum:0,
      anotherJsonNum:0,
      isAnotherFile:false,
      jsonValue:"",
      jsonKey:"",
      autoPlay:false,
      GameID:"",
      homeJsonNum:0
    }
  },
  mounted(){
    this.jsonNum=0;
    this.LoadConfig()
    this.readFile();
    this.$bus.$on("nextJson",this.nextJson)
    this.$bus.$on("implJson",this.implJson)
    this.$bus.$on("isAutoPlay",this.isAutoPlay)
    this.$bus.$on("startAnew",this.startAnew)
  },
  beforeDestroy() {
    this.$bus.$off("nextJson")
    this.$bus.$off("implJson")
    this.$bus.$off("isAutoPlay")
    this.$bus.$off("startAnew")
  },
  methods:{
    LoadConfig(){
      let configData=ReadFile('static/config.txt')
      configData=configData.replace(/[\r]/g,"");
      let configDataArr=configData.split(";\n");
      configDataArr=configDataArr.filter(i=>i && i.trim())
      let configJson=configDataArr.filter(i=>JSON.parse('{'+i+'}'))
      let img=""
      let video=""
      let audio=""

      for (let i =0;i<configJson.length;i++) {
        let configData=JSON.parse('{'+configJson[i]+'}');
        let jsonKey=Object.keys(configData)[0]
        let jsonValue=configData[jsonKey]
        if(jsonKey==="ID"){
          this.GameID=jsonValue
        }else if (jsonKey==="name"){
          document.title = jsonValue
        }else{
          if (jsonKey==="home_img")
            img=jsonValue
          else if (jsonKey==="home_video")
            video=jsonValue
          else if (jsonKey==="home_bgm")
            audio=jsonValue
        }
      }
      this.$bus.$emit("homeSet",img,video,audio)
    },
    readFile (){
      let fileData=ReadFile('static/StoryText/index.txt');
      fileData=fileData.replace(/[\r]/g,"");
      this.fileDataArr=fileData.split(";\n");
      this.fileDataArr=this.fileDataArr.filter(i=>i && i.trim())
      this.$store.state.StoryTextRowID=0
      this.$store.state.StoryTextFile='index.txt'
    },
    nextJson(){
      if(this.$store.state.clickNextSwitch)
        this.implJson();
    },
    implJson(){
      //
      let aData=""
      if (this.isAnotherFile){
        //
        if (this.anotherFileDataArr.length<=this.anotherJsonNum){
          this.jsonNum++
          this.$store.state.StoryTextRowID=this.jsonNum
          this.$store.state.StoryTextFile='index.txt'
          this.isAnotherFile=false
          return false
        }
        aData=this.anotherFileDataArr[this.anotherJsonNum].toString()
      }else{
        if (this.fileDataArr.length<=this.jsonNum)
          return false
        aData=this.fileDataArr[this.jsonNum].toString()
      }
      //
      //
      if (aData.indexOf(':')===-1){
        this.$bus.$emit('changeText','/', aData)
        this.$store.state.StoryTextRowID=this.isAnotherFile?this.anotherJsonNum:this.jsonNum
        this.isAnotherFile?this.anotherJsonNum++:this.jsonNum++
        return false
      }
      aData=aData.replace(/[\n]/g,"")
      aData=aData.replace(/[\r]/g,"")
      let jsonObj=JSON.parse('{'+aData+'}');
      this.jsonKey=Object.keys(jsonObj)[0]
      this.jsonValue=jsonObj[this.jsonKey]
      if (this.autoPlay&&this.jsonKey.indexOf("_n") === -1)
        this.jsonKey=this.jsonKey+'_n'
      console.log('{'+aData+'}');

      if (this.isAnotherFile){
        this.$store.state.StoryTextRowID=this.anotherJsonNum
        this.anotherJsonNum++
      }else{
        this.$store.state.StoryTextRowID=this.jsonNum
        this.jsonNum++;
      }
      this.handleJson(this.jsonKey,this.jsonValue);
    },
    handleJson(jsonKey,jsonValue){
      if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("c_p") !== -1 )
        this.$bus.$emit('changeImg',jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("c_v") !== -1 )
        this.$bus.$emit('changeVideo',jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("c_ms") !== -1 )
        this.$bus.$emit('changeBGMSwitch',jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("c_m") !== -1 )
        this.$bus.$emit('changeBGM',jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("c_s") !== -1 ){
        this.$bus.$emit('startInterface',jsonKey, jsonValue)
        //id
        if(this.homeJsonNum===0)
          this.homeJsonNum=this.jsonNum
      }
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("r_rt") !== -1 )
        this.$bus.$emit('roleTrans', jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("r_r") !== -1 )
        this.$bus.$emit('changeRole',jsonKey,jsonValue)
      else if(jsonKey.indexOf("css") !== -1||jsonKey.indexOf("c_ac") !== -1 )
        this.$bus.$emit('changeAnimateCss', jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("c_a") !== -1 )
        this.$bus.$emit('changeAnimate', jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("r_s") !== -1 )
        this.$bus.$emit('changeRoleSwitch', jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("t_s") !== -1 )
        this.$bus.$emit('changeTextSwitch', jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("c_t") !== -1 )
        this.$bus.$emit('imgTrans', jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("r_cr") !== -1 )
        this.$bus.$emit('clearRole', jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("r_f") !== -1 )
        this.$bus.$emit('changeFace', jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("r_l") !== -1 )
        this.$bus.$emit('changeLook', jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("t_p") !== -1 )
        this.$bus.$emit('changeMiniPic', jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("choose") !== -1 )
        this.$bus.$emit('choose', jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("s_t") !== -1 )
        this.$bus.$emit('soliloquy', jsonKey, jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("l_f") !== -1 )
        this.loadAnotherFile(jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("label_j") !== -1||jsonKey.indexOf("label_jump") !== -1 )
        this.findChooseLabel(jsonValue)
      else if(jsonKey.indexOf("") !== -1||jsonKey.indexOf("label") !== -1){
        this.jsonNum++
        this.nextJson()
      }
      else
        this.$bus.$emit('changeText',jsonKey, jsonValue)
    },
    loadAnotherFile(val){
      let fileData=ReadFile('static/StoryText/'+val);
      fileData=fileData.replace(/[\r]/g,"");
      this.anotherFileDataArr=fileData.split(";\n");
      this.anotherFileDataArr=this.anotherFileDataArr.filter(i=>i && i.trim())
      console.log(this.anotherFileDataArr);
      this.anotherJsonNum=0
      this.$store.state.StoryTextRowID=0
      this.$store.state.StoryTextFile=val
      this.isAnotherFile=true
      this.nextJson()
    },
    findChooseLabel(val){
      let jsonObj;
      let jsonKey="";
      let numStr=''
      let arrStr=''
      if(this.isAnotherFile){
        numStr='anotherJsonNum'
        arrStr='anotherFileDataArr'
      }else{
        numStr='jsonNum'
        arrStr='fileDataArr'
      }
      for (this[numStr];this[numStr]<this[arrStr].length;this[numStr]++){
        // //
        let aData=this[arrStr][this[numStr]].toString()
        //
        if (aData.indexOf(':')===-1){
          return false
        }
        jsonObj=JSON.parse('{'+aData+'}');
        jsonKey=Object.keys(jsonObj)[0]
        if (jsonKey==="label"||jsonKey===""||jsonKey===""){
          let jsonValue=jsonObj[jsonKey]
          if(jsonValue===val){
            this[numStr]++
            this.nextJson()
            return false
          }
        }
      }
      if(this.isAnotherFile){
        this.jsonNum++
        this.$store.state.StoryTextRowID=this.jsonNum
        this.$store.state.StoryTextFile='index.txt'
        this.isAnotherFile=false
        this.findChooseLabel(val)
      }
    },
    isAutoPlay(){
      this.autoPlay=!this.autoPlay
    },
    startAnew(){
      this.readFile()
      this.isAnotherFile=false
      this.anotherFileDataArr=[]
      this.jsonNum=this.homeJsonNum
      this.$bus.$emit("initTextBoard")
      this.$bus.$emit("initRole")
      let a=this.$store.state.audioBGMUrlPlay
      this.$bus.$emit("changeBGMSwitch",'',a?'1':'0')
      let b=this.$store.state.videoShow
      this.$bus.$emit("changeVideoPause",'',b?'1':'0')
    }
  },
  components: {
    Background
  }
}
</script>

