<template>
  <div style="z-index: 55;position:fixed;background-size: 100% 100%;width: 100%;height: 100%" >
    <div class="RoleHomeDiv" @mousewheel="" @mousewheel.prevent>
      <div :class="[...RoleLeftAnimClass,'Role']" style="justify-content: left;">
        <img :src="RoleLeftB64" alt="" ref="imgLeft" :style="RolePicLeftClass" v-show="imgLeftShow">
      </div>
      <div :class="[...RoleCenterAnimClass,'Role']" style="justify-content: center;">
        <img :src="RoleCenterB64" alt="" ref="imgCenter" :style="RolePicCenterClass" v-show="imgCenterShow">
      </div>
      <div :class="[...RoleRightAnimClass,'Role']" style="justify-content: right;">
        <img :src="RoleRightB64" alt="" ref="imgRight" :style="RolePicRightClass" v-show="imgRightShow">
      </div>
    </div>

  </div>
</template>

<script>
import {MCanvas} from "mcanvas";

export default {
  name: "Role",
  data(){
    return{
      RoleLeftH:"auto",
      RoleLeftW:"auto",
      RoleLeftL:"",
      RoleLeftT:"",
      RoleCenterH:"auto",
      RoleCenterW:"auto",
      RoleCenterL:"",
      RoleCenterT:"",
      RoleRightH:"auto",
      RoleRightW:"auto",
      RoleRightL:"",
      RoleRightT:"",
      RoleLeftUrl:'',
      RoleLeftLookUrl:'',
      RoleLeftFaceUrl:'',
      RoleLeftLookW:'0',
      RoleLeftLookH:'0',
      RoleLeftLookX:'0',
      RoleLeftLookY:'0',
      RoleLeftFaceW:'0',
      RoleLeftFaceH:'0',
      RoleLeftFaceX:'0',
      RoleLeftFaceY:'0',

      RoleRightUrl:'',
      RoleRightLookUrl:'',
      RoleRightFaceUrl:'',
      RoleCenterLookW:'0',
      RoleCenterLookH:'0',
      RoleCenterLookX:'0',
      RoleCenterLookY:'0',
      RoleCenterFaceW:'0',
      RoleCenterFaceH:'0',
      RoleCenterFaceX:'0',
      RoleCenterFaceY:'0',

      RoleCenterUrl:'',
      RoleCenterLookUrl:'',
      RoleCenterFaceUrl:'',
      RoleRightLookW:'',
      RoleRightLookH:'',
      RoleRightLookX:'',
      RoleRightLookY:'',
      RoleRightFaceW:'',
      RoleRightFaceH:'',
      RoleRightFaceX:'',
      RoleRightFaceY:'',
      RoleLeftB64:"",
      RoleCenterB64:"",
      RoleRightB64:"",
      RoleLeftAnimClass:['animate__animated'],
      RoleRightAnimClass:['animate__animated'],
      RoleCenterAnimClass:['animate__animated'],
      animateClassName:'',
      imgLeftShow:false,
      imgCenterShow:false,
      imgRightShow:false,
      JsonKey:''
    }
  },
  mounted() {
    this.$bus.$on('changeRole',this.changeRole)
    this.$bus.$on('clearRole',this.clearRole)
    this.$bus.$on('changeFace',this.changeFace)
    this.$bus.$on('changeLook',this.changeLook)
    this.$bus.$on('roleTrans',this.roleTrans)
    this.$bus.$on('initRole',this.initRole)

    //this.RoleLeftAnimClass.push(this.animateClassName)
  },
  methods:{
    roleTrans(key,value){
      let arr=value.split('|')
      let imgName=""
      if (key.indexOf("") !== -1||key.indexOf("r_rtl") !== -1)
        imgName='Left'
      else if (key.indexOf("") !== -1||key.indexOf("r_rtr") !== -1)
        imgName='Right'
      else if (key.indexOf("") !== -1||key.indexOf("r_rtc") !== -1||key.indexOf("") !== -1||key.indexOf("r_rt") !== -1)
        imgName='Center'
      this.$refs['img'+imgName].style.transform=arr[0]
      this.$refs['img'+imgName].style.transition='all 500ms ease'
      let AnimDuration='10'
      if (arr.length===2){
        this.$refs['img'+imgName].style.transition='all '+arr[1]+'ms ease'
        AnimDuration=arr[1]
      }
      this.$store.state['RoleTrans'+imgName]=value
      this.JsonKey=key
      if(key.indexOf("_n") !== -1){
        setTimeout(()=>{
          this.$bus.$emit("nextJson")
        },AnimDuration)
      }
    },
    //
    clearRole(jsonKey, clearVal){
      let arr=clearVal.split('|')
      let time=0;

      for(let i=0;i<arr.length;i++){
        let RolePos='',RoleAnmi=''
        let ClearRole=JSON.parse(arr[i].replaceAll("'",'"'))
        let ClearRoleKey = Object.keys(ClearRole);
        for (let i = 0; i < ClearRoleKey.length; i++) {
          RoleAnmi=''
          if (ClearRoleKey[i]===''||ClearRoleKey[i]==='p'){
            if (ClearRole[ClearRoleKey[i]]==='l'||ClearRole[ClearRoleKey[i]]===''){
              RolePos='Left'
            }else if (ClearRole[ClearRoleKey[i]]==='c'||ClearRole[ClearRoleKey[i]]===''){
              RolePos='Center'
            }else if (ClearRole[ClearRoleKey[i]]==='r'||ClearRole[ClearRoleKey[i]]===''){
              RolePos='Right'
            }
          }else if (ClearRoleKey[i]===''||ClearRoleKey[i]==='a') {
            RoleAnmi=ClearRole[ClearRoleKey[i]]
          }

        }

        if (RolePos==='') continue;

        this['Role'+RolePos+'H']="auto"
        this['Role'+RolePos+'W']="auto"
        this['Role'+RolePos+'L']=""
        this['Role'+RolePos+'T']=""
        this['Role'+RolePos+'Url']=''
        this['Role'+RolePos+'LookUrl']=''
        this['Role'+RolePos+'FaceUrl']=''
        this['Role'+RolePos+'LookW']='0'
        this['Role'+RolePos+'LookH']='0'
        this['Role'+RolePos+'LookX']='0'
        this['Role'+RolePos+'LookY']='0'
        this['Role'+RolePos+'FaceW']='0'
        this['Role'+RolePos+'FaceH']='0'
        this['Role'+RolePos+'FaceX']='0'
        this['Role'+RolePos+'FaceY']='0'
        this['Role'+RolePos+'AnimClass']=['animate__animated']
        this.$store.state['Role'+RolePos]={}
        this.$store.state['Role'+RolePos+'Face']={}
        this.$store.state['Role'+RolePos+'Look']={}
        this.$store.state['RoleTrans'+RolePos]=''
        if (RoleAnmi!==''){
          this['Role'+RolePos+'AnimClass'].push(RoleAnmi)
          time=1000
          setTimeout(() => {
            this['img'+RolePos+'Show'] = false;
          },time)
        }else{
          this.$nextTick(() => {
            this['img'+RolePos+'Show'] = false;
          })
        }
      }
      this.JsonKey=jsonKey
      if(jsonKey.indexOf("_n") !== -1){
        setTimeout(()=>{
          this.$bus.$emit("nextJson")
        },time)
      }
    },
    async loadPic(Role, RoleLook, RoleLookW, RoleLookH, RoleLookX, RoleLookY, RoleFace, RoleFaceW, RoleFaceH, RoleFaceX, RoleFaceY, RoleB64) {
      const mc = new MCanvas({
        width: 1000,
        height: 1000,
      })

      mc.background(Role, {
        type: 'origin',
      })
      if (RoleLook && RoleLook !== '') {
        mc.add(RoleLook, {
          width: RoleLookW,
          height: RoleLookH,
          pos: {
            x: RoleLookX,
            y: RoleLookY,
            scale: 1,
            rotate: 1,
          },
        })
      }
      if (RoleFace && RoleFace !== '') {
        mc.add(RoleFace, {
          width: RoleFaceW,
          height: RoleFaceH,
          pos: {
            x: RoleFaceX,
            y: RoleFaceY,
            scale: 1,
            rotate: 1,
          },
        })
      }
      this[RoleB64] = await mc.draw({type: 'png'})
    },
    //Role
    async changeRole(imgKey, imgName) {
      if (imgName === null || imgName === '') {
        this.$message.error('')
        return false;
      }
      let arr=imgName.split('|')
      let Role={};
      let RoleFace={};
      let RoleLook={};
      if (arr.length===1){
        Role=JSON.parse(arr[0].replaceAll("'",'"'))
      }
      if (arr.length===2){
        Role=JSON.parse(arr[0].replaceAll("'",'"'))
        RoleFace=JSON.parse(arr[1].replaceAll("'",'"'))
      }
      if (arr.length===3){
        Role=JSON.parse(arr[0].replaceAll("'",'"'))
        RoleFace=JSON.parse(arr[1].replaceAll("'",'"'))
        RoleLook=JSON.parse(arr[2].replaceAll("'",'"'))
      }
      let RolePos=''
      if (imgKey.indexOf("") !== -1||imgKey.indexOf("r_rl") !== -1){
        await this.RoleSetting(Role, RoleFace, RoleLook, 'Left',arr.length)
        RolePos='Left'
      }
      else if (imgKey.indexOf("") !== -1||imgKey.indexOf("r_rr") !== -1){
        await this.RoleSetting(Role, RoleFace, RoleLook, 'Right',arr.length)
        RolePos='Right'
      }
      else if (imgKey.indexOf("") !== -1||imgKey.indexOf("") !== -1||imgKey.indexOf("r_r") !== -1){
        await this.RoleSetting(Role, RoleFace, RoleLook, 'Center',arr.length)
        RolePos='Center'
      }
      if (RolePos!==''){
        this.$store.state['Role'+RolePos]=Role
        this.$store.state['Role'+RolePos+'Look']=RoleLook
        this.$store.state['Role'+RolePos+'Face']=RoleFace
        this.$store.state['RoleTrans'+RolePos]=''
      }
      this.JsonKey=imgKey
      if(imgKey.indexOf("_n") !== -1){
        setTimeout(()=>{
          this.$bus.$emit("nextJson")
        },1000)
      }
    },
    //
    async RoleSetting(Role, RoleFace, RoleLook,RolePos,ArrLen) {
      this['Role'+RolePos+'H']="auto"
      this['Role'+RolePos+'W']="auto"
      this['Role'+RolePos+'L']=""
      this['Role'+RolePos+'T']=""
      this['Role'+RolePos+'Url']=''
      this['Role'+RolePos+'LookUrl']=''
      this['Role'+RolePos+'FaceUrl']=''
      this['Role'+RolePos+'LookW']='0'
      this['Role'+RolePos+'LookH']='0'
      this['Role'+RolePos+'LookX']='0'
      this['Role'+RolePos+'LookY']='0'
      this['Role'+RolePos+'FaceW']='0'
      this['Role'+RolePos+'FaceH']='0'
      this['Role'+RolePos+'FaceX']='0'
      this['Role'+RolePos+'FaceY']='0'
      this['Role'+RolePos+'AnimClass']=['animate__animated']
      let RoleKey = Object.keys(Role);
      for (let i = 0; i < RoleKey.length; i++) {
        if (RoleKey[i] === '') {
          this['Role'+RolePos+'Url'] = Role[RoleKey[i]]
          this['Role'+RolePos+'B64'] = './static/RoleSource/' + Role[RoleKey[i]]
        } else if (["width","","w"].indexOf(RoleKey[i])!== -1)
          this['Role'+RolePos+'W'] = Role[RoleKey[i]]
        else if (["height","","h"].indexOf(RoleKey[i])!== -1)
          this['Role'+RolePos+'H'] = Role[RoleKey[i]]
        else if (["left","","l"].indexOf(RoleKey[i])!== -1)
          this['Role'+RolePos+'L'] = Role[RoleKey[i]]
        else if (["top","","","t"].indexOf(RoleKey[i])!== -1)
          this['Role'+RolePos+'T'] = Role[RoleKey[i]]
        else if (RoleKey[i]===''){
          this.animateClassName=Role[RoleKey[i]]
          this['Role'+RolePos+'AnimClass'].push(this.animateClassName)
        }
      }
      let RoleFaceKey = Object.keys(RoleFace);
      for (let i = 0; i < RoleFaceKey.length; i++) {
        if (RoleFaceKey[i] === '')
          this['Role'+RolePos+'FaceUrl'] = RoleFace[RoleFaceKey[i]]
        else if (["width","","w"].indexOf(RoleKey[i])!== -1)
          this['Role'+RolePos+'FaceW'] = RoleFace[RoleFaceKey[i]]
        else if (["height","","h"].indexOf(RoleKey[i])!== -1)
          this['Role'+RolePos+'FaceH'] = RoleFace[RoleFaceKey[i]]
        else if (RoleFaceKey[i] === 'x')
          this['Role'+RolePos+'FaceX'] = RoleFace[RoleFaceKey[i]]
        else if (RoleFaceKey[i] === 'y')
          this['Role'+RolePos+'FaceY'] = RoleFace[RoleFaceKey[i]]
      }
      let RoleLookKey = Object.keys(RoleLook);
      for (let i = 0; i < RoleLookKey.length; i++) {
        if (RoleLookKey[i] === '')
          this['Role'+RolePos+'LookUrl'] = RoleLook[RoleLookKey[i]]
        else if (["width","","w"].indexOf(RoleKey[i])!== -1)
          this['Role'+RolePos+'LookW'] = RoleLook[RoleLookKey[i]]
        else if (["height","","w"].indexOf(RoleKey[i])!== -1)
          this['Role'+RolePos+'LookH'] = RoleLook[RoleLookKey[i]]
        else if (RoleLookKey[i] === 'x')
          this['Role'+RolePos+'LookX'] = RoleLook[RoleLookKey[i]]
        else if (RoleLookKey[i] === 'y')
          this['Role'+RolePos+'LookY'] = RoleLook[RoleLookKey[i]]
      }
      this['img'+RolePos+'Show'] = false;
      if (ArrLen.length !== 1) {
        await this.loadPic('./static/RoleSource/' + this['Role'+RolePos+'Url'],
            this['role'+RolePos+'LookFile'], this['Role'+RolePos+'LookW'],
            this['Role'+RolePos+'LookH'], this['Role'+RolePos+'LookX'],
            this['Role'+RolePos+'LookY'], this['role'+RolePos+'FaceFile'],
            this['Role'+RolePos+'FaceW'], this['Role'+RolePos+'FaceH'],
            this['Role'+RolePos+'FaceX'], this['Role'+RolePos+'FaceY'], 'Role'+RolePos+'B64')
      }

      this.$nextTick(() => {
        this['img'+RolePos+'Show'] = true;
      })
    },
    //
    async changeFace(jsonKey, val) {
      if (val === null || val === '') {
        this.$message.error('')
        return false;
      }
      let arr = val.split('|')
      for (let i = 0; i < arr.length; i++) {
        let obj = JSON.parse(arr[i].replaceAll("'", '"'))
        let objKey = Object.keys(obj)
        let RolePos = '', faceUrl = '',faceW = '',faceH = '',faceX = '',faceY = '',faceWKey='',faceHKey=''
        for (let j = 0; j < objKey.length; j++) {
          if (objKey[j] === '' || objKey[j] === 'p') {
            if (obj[objKey[j]] === 'l' || obj[objKey[j]] === '') {
              RolePos = 'Left'
            } else if (obj[objKey[j]] === 'c' || obj[objKey[j]] === '') {
              RolePos = 'Center'
            } else if (obj[objKey[j]] === 'r' || obj[objKey[j]] === '') {
              RolePos = 'Right'
            }
          }
          else if (objKey[j] === '')
            faceUrl = obj[objKey[j]]
          else if (["width", "", "w"].indexOf(objKey[j]) !== -1){
            faceW = obj[objKey[j]]
            faceWKey=objKey[j]
          }
          else if (["height", "", "h"].indexOf(objKey[j]) !== -1){
            faceH = obj[objKey[j]]
            faceHKey = objKey[j]
          }
          else if (objKey[j] === 'x')
            faceX = obj[objKey[j]]
          else if (objKey[j] === 'y')
            faceY = obj[objKey[j]]
        }
        if (RolePos === '') continue
        if (!this['img'+RolePos+'Show']) continue
        this['Role' + RolePos + 'FaceUrl'] = faceUrl
        this.$set(this.$store.state['Role'+RolePos+'Face'],'',faceUrl)
        this['Role' + RolePos + 'FaceW'] = faceW
        this.$set(this.$store.state['Role'+RolePos+'Face'],faceWKey,faceW)
        this['Role' + RolePos + 'FaceH'] = faceH
        this.$set(this.$store.state['Role'+RolePos+'Face'],faceHKey,faceH)
        this['Role' + RolePos + 'FaceX'] = faceX
        this.$set(this.$store.state['Role'+RolePos+'Face'],'x',faceX)
        this['Role' + RolePos + 'FaceY'] = faceY
        this.$set(this.$store.state['Role'+RolePos+'Face'],'y',faceY)
        console.log(this.$store.state);

        await this.loadPic('./static/RoleSource/' + this['Role' + RolePos + 'Url'],
            this['role' + RolePos + 'LookFile'], this['Role' + RolePos + 'LookW'],
            this['Role' + RolePos + 'LookH'], this['Role' + RolePos + 'LookX'],
            this['Role' + RolePos + 'LookY'], this['role' + RolePos + 'FaceFile'],
            this['Role' + RolePos + 'FaceW'], this['Role' + RolePos + 'FaceH'],
            this['Role' + RolePos + 'FaceX'], this['Role' + RolePos + 'FaceY'], 'Role' + RolePos + 'B64')
      }
      this.JsonKey=jsonKey
      if(jsonKey.indexOf("_n") !== -1){
        setTimeout(()=>{
          this.$bus.$emit("nextJson")
        },10)
      }
    },
    async changeLook(jsonKey, val) {
      if (val === null || val === '') {
        this.$message.error('')
        return false;
      }
      let arr = val.split('|')
      for (let i = 0; i < arr.length; i++) {
        let obj = JSON.parse(arr[i].replaceAll("'", '"'))
        let objKey = Object.keys(obj)
        let RolePos = '', lookUrl = '',lookW = '',lookH = '',lookX = '',lookY = '',lookWKey='',lookHKey=''
        for (let j = 0; j < objKey.length; j++) {
          if (objKey[j] === '' || objKey[j] === 'p') {
            if (obj[objKey[j]] === 'l' || obj[objKey[j]] === '') {
              RolePos = 'Left'
            } else if (obj[objKey[j]] === 'c' || obj[objKey[j]] === '') {
              RolePos = 'Center'
            } else if (obj[objKey[j]] === 'r' || obj[objKey[j]] === '') {
              RolePos = 'Right'
            }
          }
          else if (objKey[j] === '')
            lookUrl = obj[objKey[j]]
          else if (["width", "", "w"].indexOf(objKey[j]) !== -1){
            lookW = obj[objKey[j]]
            lookWKey=objKey[j]
          }
          else if (["height", "", "h"].indexOf(objKey[j]) !== -1){
            lookH = obj[objKey[j]]
            lookHKey = objKey[j]
          }
          else if (objKey[j] === 'x')
            lookX = obj[objKey[j]]
          else if (objKey[j] === 'y')
            lookY = obj[objKey[j]]
        }
        if (RolePos === '') continue
        if (!this['img'+RolePos+'Show']) continue
        this['Role' + RolePos + 'LookUrl'] = lookUrl
        this.$set(this.$store.state['Role'+RolePos+'Look'],'',lookUrl)
        this['Role' + RolePos + 'LookW'] = lookW
        this.$set(this.$store.state['Role'+RolePos+'Look'],lookWKey,lookW)
        this['Role' + RolePos + 'LookH'] = lookH
        this.$set(this.$store.state['Role'+RolePos+'Look'],lookHKey,lookH)
        this['Role' + RolePos + 'LookX'] = lookX
        this.$set(this.$store.state['Role'+RolePos+'Look'],'x',lookX)
        this['Role' + RolePos + 'LookY'] = lookY
        this.$set(this.$store.state['Role'+RolePos+'Look'],'y',lookY)
        console.log(this.$store.state);

        await this.loadPic('./static/RoleSource/' + this['Role' + RolePos + 'Url'],
            this['role' + RolePos + 'LookFile'], this['Role' + RolePos + 'LookW'],
            this['Role' + RolePos + 'LookH'], this['Role' + RolePos + 'LookX'],
            this['Role' + RolePos + 'LookY'], this['role' + RolePos + 'FaceFile'],
            this['Role' + RolePos + 'FaceW'], this['Role' + RolePos + 'FaceH'],
            this['Role' + RolePos + 'FaceX'], this['Role' + RolePos + 'FaceY'], 'Role' + RolePos + 'B64')
      }
      this.JsonKey=jsonKey
      if(jsonKey.indexOf("_n") !== -1){
        setTimeout(()=>{
          this.$bus.$emit("nextJson")
        },10)
      }
    },
    initRole(){
      this.clearRole("", "{'p':'l'}|{'p':'c'}|{'p':'r'}")
    }
  },
  computed:{
    async roleLeftFile() {      //  
      if (this.RoleLeftUrl !== '') {
        return './static/RoleSource/' + this.RoleLeftUrl
      } else
        return false;
    },
    roleCenterFile(){      //  
      if (this.RoleCenterUrl!=='')
        return './static/RoleSource/'+this.RoleCenterUrl
      else
        return false;
    },
    roleRightFile(){      //  
      if (this.RoleRightUrl!=='')
        return './static/RoleSource/'+this.RoleRightUrl
      else
        return false;
    },
    roleLeftLookFile(){      //  
      if (this.RoleLeftLookUrl!=='')
        return './static/RoleSource/'+this.RoleLeftLookUrl
      else
        return false;
    },
    roleCenterLookFile(){      //  
      if (this.RoleCenterLookUrl!=='')
        return './static/RoleSource/'+this.RoleCenterLookUrl
      else
        return false;
    },
    roleRightLookFile(){      //  
      if (this.RoleRightLookUrl!=='')
        return './static/RoleSource/'+this.RoleRightLookUrl
      else
        return false;
    },
    roleLeftFaceFile(){      //  
      if (this.RoleLeftFaceUrl!=='')
        return './static/RoleSource/'+this.RoleLeftFaceUrl
      else
        return false;
    },
    roleCenterFaceFile(){      //  
      if (this.RoleCenterFaceUrl!=='')
        return './static/RoleSource/'+this.RoleCenterFaceUrl
      else
        return false;
    },
    roleRightFaceFile(){      //  
      if (this.RoleRightFaceUrl!=='')
        return './static/RoleSource/'+this.RoleRightFaceUrl
      else
        return false;
    },
    RolePicLeftClass(){
      return `position:absolute;width: ${this.RoleLeftW}; height: ${this.RoleLeftH};${this.RoleLeftL!==''?`left: ${this.RoleLeftL};`:''}${this.RoleLeftT!==''?`top: ${this.RoleLeftT};`:''}`
    },
    RolePicCenterClass(){
      return `position:absolute;width: ${this.RoleCenterW}; height: ${this.RoleCenterH};${this.RoleCenterL!==''?`left: ${this.RoleCenterL};`:''}${this.RoleCenterT!==''?`top: ${this.RoleCenterT};`:''}`
    },
    RolePicRightClass(){
      return `position:absolute;width: ${this.RoleRightW}; height: ${this.RoleRightH};${this.RoleRightL!==''?`left: ${this.RoleRightL};`:''}${this.RoleRightT!==''?`top: ${this.RoleRightT};`:''}`
    }
  },
  beforeDestroy() {
    this.$bus.$off('changeRole')
    this.$bus.$off('clearRole')
    this.$bus.$off('changeFace')
    this.$bus.$off('changeLook')
    this.$bus.$off('roleTrans')
    this.$bus.$off('initRole')
  }
}
</script>

<style scoped>
.RoleHomeDiv{
  display: flex;
  display: -webkit-flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
}

.Role{
  display: flex;
  width: 100%;
  height: 200%;
  position: absolute;
  left: 5px;
  top: 10px
}

.RolePicClass{
  /*max-height: 100%;*/
  /*max-width: 100%;*/
 /* display: block;*/
  width: auto;
  height: auto;
}
</style>
