<template>
  <div>
    <img :src="src" alt="s"/>
    <el-button type="warning" @click="loadPic" style="bottom: 0;position: absolute"></el-button>
  </div>
</template>

<script>
import { MCanvas } from 'mcanvas'
export default {
  name: "demo3",
  data(){
    return{
      src:""
    }
  },
  methods:{
    async loadPic() {
      const mc = new MCanvas({
        width: 1000,
        height: 1000,
        backgroundColor: 'black',
      })

      mc.background('./static/RoleSource/.png', {
        left: 0,
        top: 0,
        color: '#000000',
        type: 'origin',
      })
      mc.add('./static/RoleSource/.png', {
        width: 183,
        pos: {
          x: 250,
          y: 369,
          scale: 0.84,
          rotate: 1,
        },
      })
      this.src = await mc.draw({type: 'png'});
    }
  }
}
</script>

