<template>
  <div style="z-index: 85;position:fixed;display:flex;background-size: 100% 100%;width: 100%;height: 100%;"
       @click="setBoardClick($event)" class="SettingBackground">
    <div class="SettingContext" @click="setContextClick($event)">
      <div class="SettingCloseDiv" @click="closeSetting">
        <i class="SettingCloseIcon"></i>
      </div>
      <div class="SettingFontSizeDiv">
        <span class="SettingFontSizeSpan"></span>

        <el-slider v-model="fontSizeValue"
                   class="SettingFontSizeSlider" :min="100" :max="200">
        </el-slider>
        <el-input-number v-model="fontSizeValue" class="SettingFontSizeInput"
                         :min="100" :max="200" :controls="false" style="padding-left:5%"></el-input-number>
      </div>
      <div class="SettingPlaySpeedDiv">
        <span class="SettingPlaySpeedSpan"></span>

        <el-slider v-model="playSpeedValue"
                   class="SettingPlaySpeedSlider" :min="50" :max="200" @change="playSpeedChange">
        </el-slider>
        <el-input-number v-model="playSpeedValue" class="SettingPlaySpeedInput"
                         :min="50" :max="200" :controls="false" style="padding-left:5%"></el-input-number>
      </div>
      <div class="SettingDisplayModeDiv">
        <span class="SettingDisplayModeSpan"></span>
        <el-select v-model="fontDisplayMode" placeholder="" class="DisplayModeSelect" @change="fontDisplayModeChange">
          <el-option
              v-for="(item,index) in displayMode"
              :key="index"
              :label="item.label"
              :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="SettingVolumeDiv">
        <span class="SettingVolumeSpan"></span>
        <el-slider v-model="playVolumeValue"
                   class="SettingVolumeSlider" :min="0" :max="100">
        </el-slider>
      </div>
      <div class="Setting3ButtonDiv">
        <div class="SettingImportDiv">
          <el-button class="SettingImportButton"></el-button>
        </div>
        <div class="SettingExportDiv">
          <el-button class="SettingExportButton"></el-button>
        </div>
        <div class="SettingClearAllDiv">
          <el-button class="SettingClearAllButton"></el-button>
        </div>
      </div>
      <div class="SettingViewDiv">
        <span class="SettingViewSpan"></span>
        <div class="SettingViewContent">
          <div class="TextBoardContentBackGround">
            <div :class="[...textClass,fontDisplayMode]" :style="'color: white;font-size: '+fontSizeValue+'%'" ref="textRef">
              <span v-for="(t,index) in textContent" class="letter"
                    :style="'animation-delay:'+(index*playSpeedValue)+'ms;'" >
                {{t===' '?'&nbsp;':t}}</span>
            </div>
          </div>
        </div>
      </div>
      <div style="text-align: center;"><i>Powered by </i><b class="">GalGameEngine</b></div>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
let timer;
export default {
  name: "Setting",
  data(){
    return{
      fontSizeValue:150,
      playSpeedValue:100,
      playVolumeValue:100,
      fontDisplayMode:'leFadeIn',
      textContent:'~',
      textClass:['cssanimation','sequence'],
      displayMode:[{"value":"leFadeInLeft","label":""}
        ,{"value":"leFadeInRight","label":""},
        {"value":"leFadeIn","label":""},
        {"value":"leFadeInBottom","label":""}],
      animateTime:0
    }
  },
  methods: {
    setBoardClick(e){
      this.closeSetting()
      e.stopPropagation()
    },
    setContextClick(e){
      e.stopPropagation()
    },
    startTimer(){
      let text=this.textContent
      clearInterval(timer)
      this.animateTime=((this.textContent.length + 2) * this.playSpeedValue)
      timer=setInterval(()=>{
        this.textContent=''
        this.$refs.textRef.classList.remove(this.fontDisplayMode)
        this.$nextTick(()=>{
          this.textContent=text
          this.$refs.textRef.classList.add(this.fontDisplayMode)
        })
      },this.animateTime)
    },
    stopTimer(){
      clearInterval(timer)
      this.$refs.textRef.classList.remove(this.fontDisplayMode)
      this.textContent=''
    },
    playSpeedChange: _.debounce(function (){
      this.startTimer()
    }),
    fontDisplayModeChange(){
      this.startTimer()
    },
    closeSetting(){
      this.stopTimer()
      this.$emit("CloseSetting")
    },
    imgBeforeEnter(el){
      el.style.opacity = 0
    },
    imgEnter(el,done){
      // 500ms 1
      Velocity(el, {
        opacity: 1
      }, {
        //  done
        duration: 500,
        complete:done
      })
    },
    imgAfterEnter(){
      //
    },
    imgLeave(el,done){
      Velocity(el, {
        opacity: 0
      }, {
        //  done
        duration: 300,
        complete:done
      })
    },
    imgBeforeLeave(el){
      el.style.opacity =1
    },
    imgAfterLeave(el){
      //
    }
  },
  mounted() {
    this.fontSizeValue=localStorage.getItem('fontSizeValue')?parseInt(localStorage.getItem('fontSizeValue')):150
    this.playVolumeValue=localStorage.getItem('playVolumeValue')?parseInt(localStorage.getItem('playVolumeValue')):100
    this.fontDisplayMode=localStorage.getItem('fontDisplayMode')?localStorage.getItem('fontDisplayMode'):"leFadeIn"
    this.playSpeedValue=localStorage.getItem('playSpeedValue')?parseInt(localStorage.getItem('playSpeedValue')):100
    this.startTimer()
  },
  beforeDestroy() {
    localStorage.setItem("fontSizeValue",this.fontSizeValue)
    localStorage.setItem("playVolumeValue",this.playVolumeValue)
    localStorage.setItem("fontDisplayMode",this.fontDisplayMode)
    localStorage.setItem("playSpeedValue",this.playSpeedValue)
    this.$bus.$emit("useNewSetting",{"fontSizeValue":this.fontSizeValue,
      "fontDisplayMode":this.fontDisplayMode,"playSpeedValue":this.playSpeedValue})
    this.$bus.$emit("setNewVolume",this.playVolumeValue)
  },
}
</script>

